export function serializeInsufficientMaterialState(state) {
    const backwardsPieceRegistry = {};
    const counters = {};
    let individualCounter = 0;
    let key;
    for (key in state.backwardsPieceRegistry) {
        if (!Object.prototype.hasOwnProperty.call(state.backwardsPieceRegistry, key))
            continue;
        backwardsPieceRegistry[key] = state.backwardsPieceRegistry[key].map((r) => r.map((e) => {
            if (e !== null) {
                if (e in counters) {
                    return counters[e];
                }
                else {
                    counters[e] = individualCounter++;
                    return counters[e];
                }
            }
            else
                return e;
        }));
    }
    const pieceSquareMedians = {};
    for (key in state.pieceSquareMedians) {
        if (!Object.prototype.hasOwnProperty.call(state.pieceSquareMedians, key))
            continue;
        const result = {};
        for (const symbol of Object.getOwnPropertySymbols(state.pieceSquareMedians[key])) {
            if (symbol in counters) {
                result[counters[symbol]] = state.pieceSquareMedians[key][symbol];
            }
            else
                result[individualCounter--] = state.pieceSquareMedians[key][symbol];
        }
        pieceSquareMedians[key] = result;
    }
    return {
        ...state,
        backwardsPieceRegistry,
        pieceSquareMedians,
        optimizedPieces: [...state.optimizedPieces]
    };
}
export function deserializeInsufficientMaterialState(state) {
    const backwardsPieceRegistry = {};
    const counters = {};
    let key;
    for (key in state.backwardsPieceRegistry) {
        if (!Object.prototype.hasOwnProperty.call(state.backwardsPieceRegistry, key))
            continue;
        counters[key] = {};
        backwardsPieceRegistry[key] = state.backwardsPieceRegistry[key].map((r) => r.map((e) => {
            if (e !== null) {
                if (!(e in counters[key]))
                    counters[key][e] = Symbol();
                return counters[key][e];
            }
            else
                return e;
        }));
    }
    const pieceSquareMedians = {};
    for (key in state.pieceSquareMedians) {
        if (!Object.prototype.hasOwnProperty.call(state.pieceSquareMedians, key))
            continue;
        const result = {};
        for (const numericKey in state.pieceSquareMedians[key]) {
            if (!Object.prototype.hasOwnProperty.call(state.pieceSquareMedians[key], numericKey))
                continue;
            const indexingNumber = Number(numericKey);
            if (key in counters && indexingNumber in counters[key]) {
                result[counters[key][indexingNumber]] = state.pieceSquareMedians[key][indexingNumber];
            }
            else
                result[Symbol()] = state.pieceSquareMedians[key][indexingNumber];
        }
        pieceSquareMedians[key] = result;
    }
    return {
        ...state,
        backwardsPieceRegistry,
        pieceSquareMedians,
        optimizedPieces: new Set(state.optimizedPieces)
    };
}
