export function createBooleanTag(parameters) {
    const { defaultValue, key, isStatic } = parameters;
    const result = {
        value: defaultValue,
        parse(input) {
            return typeof input === "boolean" ? input : this.createSnapshot();
        },
        serialize() {
            return this.value === defaultValue ? undefined : `'${key}':${String(this.value)}`;
        },
        createSnapshot() {
            return this.value;
        },
        loadSnapshot(snapshot) {
            this.value = snapshot;
        }
    };
    if (isStatic)
        delete result.loadSnapshot;
    return result;
}
const defaultDimension = [14, 14];
export const createDimensionTag = () => ({
    value: [...defaultDimension],
    parse(baseInput) {
        if (typeof baseInput !== "string")
            return this.createSnapshot();
        const input = baseInput.split("x");
        return Array.isArray(input) &&
            verifyTupleType(input, 2) &&
            input.every((i) => {
                const v = Number(i);
                return !isNaN(v) && v >= 2 && v <= 14 && v % 2 === 0;
            })
            ? input.map((v) => Number(v)).reverse()
            : this.createSnapshot();
    },
    serialize() {
        if (compareArrays(this.value, defaultDimension))
            return;
        return `'dim':'${this.value.join("x")}'`;
    },
    createSnapshot() {
        return [...this.value];
    }
});
const defaultZombies = createTuple(ZombieType.Rando, totalPlayers);
export const createZombiesTag = () => ({
    value: [...defaultZombies],
    parse(input) {
        if (Array.isArray(input) && verifyTupleType(input, totalPlayers) && input.every((v) => typeof v === "string")) {
            const newZombies = this.createSnapshot();
            for (const color of colors) {
                const zombieType = input[color];
                if (verifyZombieType(zombieType))
                    newZombies[color] = zombieType;
            }
            return newZombies;
        }
        else
            return this.createSnapshot();
    },
    serialize() {
        if (compareArrays(this.value, defaultZombies))
            return;
        return `'zombieType':(${this.value.map((t, i) => (t === defaultZombies[i] ? "''" : `'${t}'`)).join(",")})`;
    },
    createSnapshot() {
        return [...this.value];
    }
});
export const createRoyalTag = () => ({
    value: createTuple(null, totalPlayers),
    parse(input) {
        if (verifyParameterType(input, "string")) {
            return input.map((c) => parseSingleCoordinate(c) ?? null);
        }
        else
            return this.createSnapshot();
    },
    serialize() {
        return `'royal':(${this.value.map((r) => (r ? `'${convertCoordinateToPGN4(r)}'` : "''")).join(",")})`;
    },
    createSnapshot() {
        return this.value.map((c) => (c ? [...c] : c));
    },
    loadSnapshot(snapshot) {
        this.value = snapshot.map((c) => (c ? [...c] : c));
    }
});
export const createLivesTag = () => ({
    value: createTuple(null, totalPlayers),
    parse(input) {
        if (verifyParameterType(input, "number")) {
            return input;
        }
        else
            return this.createSnapshot();
    },
    serialize() {
        if (this.value.some((v) => v !== null)) {
            return `'lives':(${this.value.map((l) => (l ? String(l) : "1")).join(",")})`;
        }
        else
            return;
    },
    createSnapshot() {
        return [...this.value];
    },
    loadSnapshot(snapshot) {
        this.value = [...snapshot];
    }
});
export const createBankTag = () => ({
    value: createTupleFromCallback(() => new Map(), totalPlayers),
    parse(input) {
        const defaultBanks = createTupleFromCallback(() => new Map(), totalPlayers);
        if (verifyParameterType(input, "string")) {
            for (const color of colors) {
                for (const pieceDefinition of input[color].split(",")) {
                    const [piece, count = 1] = pieceDefinition.split("x").map((e, i) => (i === 1 ? Number(e) : e));
                    if (typeof piece !== "string" || typeof count !== "number")
                        continue;
                    if (!/^(?:r|b|y|g).$/.test(piece))
                        continue;
                    const pieceLetter = piece.charAt(1);
                    if (verifyPieceLetter(pieceLetter)) {
                        defaultBanks[color].set(createPieceFromString(piece), count);
                    }
                }
            }
        }
        return defaultBanks;
    },
    serialize() {
        if (this.value.some((b) => b.size !== 0)) {
            return `'bank':(${this.value
                .map((bank) => {
                const resultingStrings = [];
                for (const [piece, count] of bank) {
                    if (count === 1) {
                        resultingStrings.push(piece.value);
                    }
                    else {
                        resultingStrings.push(`${piece.value}x${count}`);
                    }
                }
                return `'${resultingStrings.join(",")}'`;
            })
                .join(",")})`;
        }
        else
            return;
    },
    createSnapshot() {
        return this.value.map((m) => [...m.entries()].map(([pieceString, count]) => [pieceString.toObject(), count]));
    },
    loadSnapshot(snapshot) {
        this.value = snapshot.map((v) => new Map(v.map(([pieceStringObject, count]) => [PieceString.fromObjectToClass(pieceStringObject), count])));
    }
});
export const createPromotedFromTag = () => ({
    value: new Map(),
    parse(input) {
        const promotedFromMap = new Map();
        if (!verifyObjectType(input))
            return promotedFromMap;
        for (const key in input) {
            if (!Object.prototype.hasOwnProperty.call(promotedFromMap, key))
                continue;
            const parsedCoordinate = parseSingleCoordinate(key);
            const pieceLetter = input[key];
            if (!parsedCoordinate || typeof pieceLetter !== "string" || !verifyPieceLetter(pieceLetter))
                continue;
            promotedFromMap.set(parsedCoordinate, pieceLetter);
        }
        return promotedFromMap;
    },
    serialize() {
        if (this.value.size === 0)
            return;
        const promotedFromStrings = [];
        for (const [coordinate, piece] of this.value) {
            promotedFromStrings.push(`'${convertCoordinateToPGN4(coordinate)}':'${piece}'`);
        }
        return `'promotedFrom':{${promotedFromStrings.join(",")}}`;
    },
    createSnapshot() {
        const newMap = [];
        for (const [coordinate, pieceLetter] of this.value.entries()) {
            newMap.push([[...coordinate], pieceLetter]);
        }
        return newMap;
    },
    loadSnapshot(snapshot) {
        this.value.clear();
        for (const [coordinate, pieceLetter] of snapshot) {
            this.value.set([...coordinate], pieceLetter);
        }
    }
});
export const createSetupPointsTag = () => ({
    value: null,
    parse(input) {
        if (verifyParameterType(input, "number")) {
            return input;
        }
        else
            return this.createSnapshot();
    },
    serialize() {
        if (this.value) {
            return `'setupPoints':(${this.value.join(",")})`;
        }
        else
            return;
    },
    createSnapshot() {
        return this.value ? [...this.value] : this.value;
    },
    loadSnapshot(snapshot) {
        this.value = snapshot ? [...snapshot] : snapshot;
    }
});
export const createSeirawanDropsTag = () => ({
    value: createTupleFromCallback(() => new Set(), totalPlayers),
    parse(input) {
        const defaultValue = createTupleFromCallback(() => new Set(), totalPlayers);
        if (Array.isArray(input) &&
            (verifyTupleType(input, totalPlayers) || verifyTupleType(input, totalPlayers + 1)) &&
            input.every((v) => Array.isArray(v))) {
            for (const color of colors) {
                const seirawanDropsArray = input[color];
                if (!seirawanDropsArray.every((v) => typeof v === "string"))
                    continue;
                for (const pieceDefinition of seirawanDropsArray) {
                    if (!PGN4_SYNTAX.COORDINATE_REGEX.test(pieceDefinition))
                        continue;
                    const coordinate = parseSingleCoordinate(pieceDefinition);
                    if (coordinate) {
                        defaultValue[color].add(stringifyCoordinate(coordinate));
                    }
                }
            }
        }
        return defaultValue;
    },
    serialize() {
        if (this.value.every((m) => m.size === 0))
            return;
        return `'seirawanDrops':(${this.value
            .map((coordinates) => {
            return `(${[...coordinates].map((coord) => `'${convertCoordinateToPGN4(unstringifyCoordinate(coord))}'`).join(",")})`;
        })
            .join(",")})`;
    },
    createSnapshot() {
        return this.value.map((s) => [...s.values()]);
    },
    loadSnapshot(snapshot) {
        this.value = snapshot.map((s) => new Set(s));
    }
});
