export function formatOrdinalNumber(num) {
    const lastTwoDigits = Number(String(num).slice(-2));
    if (lastTwoDigits > 3 && lastTwoDigits < 21)
        return "th";
    switch (lastTwoDigits % 10) {
        case 1:
            return "st";
        case 2:
            return "nd";
        case 3:
            return "rd";
        default:
            return "th";
    }
}
export function compileEnumeration(list) {
    if (list.length === 0)
        return "";
    if (list.length === 1)
        return list[0];
    let result = "";
    for (let i = 0; i < list.length; i++) {
        if (i === list.length - 2) {
            result += ` ${list[i]} or `;
        }
        else if (i === list.length - 1) {
            result += list[i];
        }
        else {
            result += list[i] + ", ";
        }
    }
    return result;
}
export function prefixWithIndefiniteArticle(str) {
    const vowels = /[AEUIO]/i;
    return vowels.test(str.charAt(0)) ? "an " + str : "a " + str;
}
export function convertSecondsToFlexibleHoursMinutesSeconds(seconds) {
    if (seconds >= 3600) {
        return new Date(seconds * 1000).toISOString().substring(11, 16);
    }
    else {
        return new Date(seconds * 1000).toISOString().substring(14, 19);
    }
}
export function convertCamelCaseToKebabCase(baseString) {
    return baseString.replace(/([a-z])([A-Z])/g, "$1-$2").toLowerCase();
}
export function hashString(string) {
    let hash = 0;
    for (let i = 0; i < string.length; i++) {
        const char = string.charCodeAt(i);
        hash = (hash << 5) - hash + char;
        hash |= 0;
    }
    return hash;
}
