import { verifyNumericColor } from "@moveGeneration/GameInformation/GameUnits/GameUnits";
import { totalPlayers } from "../GameInformation/GameData";
export const createDefaultFENEffectSettings = () => ({
    ignoreCheckmateChecks: false,
    ignoreNextTurn: false
});
export const createDefaultArmyDeathSettings = () => ({
    excludeRoyals: false,
    onlyPawns: false,
    doNotSetDead: false
});
export function getNeighboringSideToMove(player, dead) {
    let sideToMove = player;
    do {
        sideToMove = sideToMove === totalPlayers - 1 ? 0 : sideToMove + 1;
    } while (dead[sideToMove]);
    if (verifyNumericColor(sideToMove)) {
        return sideToMove;
    }
    else
        throw new Error(`Unexpected numeric color: ${sideToMove}`);
}
