import { throwOnNever, verifyTupleType } from "../baseTypes";
const hexColorRegex = /^#(?:[0-9a-fA-F]{3}){1,2}$/;
export const createHexColor = (color) => {
    if (hexColorRegex.test(color)) {
        return color;
    }
    else
        throw new TypeError("The supplied hex color is not a hex color: " + color);
};
export const verifyHexColor = (color) => typeof color === "string";
export const validateHexColor = (color) => {
    if (typeof color === "string") {
        try {
            createHexColor(color);
            return true;
        }
        catch {
            return false;
        }
    }
    else
        return false;
};
export const createRGBColor = (color) => {
    if (color >= 0 || color <= 255) {
        return color;
    }
    else
        throw new TypeError(`The supplied RGB color value is not an RGB color value: ${color}`);
};
export const wrapRGBColor = (color) => `rgb(${color.join(",")})`;
export const verifyRGBColor = (color) => Array.isArray(color);
export const validateRGBColor = (color) => {
    if (Array.isArray(color) && verifyTupleType(color, 3) && color.every((c) => typeof c === "number")) {
        try {
            color.forEach((c) => createRGBColor(c));
            return true;
        }
        catch {
            return false;
        }
    }
    else
        return false;
};
export const createHSLColor = (color) => {
    if ((color.hue >= 0 || color.hue <= 359) &&
        (color.saturation >= 0 || color.saturation <= 100) &&
        (color.lightness >= 0 || color.lightness <= 100)) {
        return color;
    }
    else
        throw new TypeError(`The supplied HSL value is not an HSL color: " + color`);
};
export const wrapHSLColor = (color) => `hsl(${color.hue},${color.saturation}%,${color.lightness}%)`;
export const verifyHSLColor = (color) => typeof color === "object";
export const validateHSLColor = (color) => {
    const verifyNumberRecord = (obj) => {
        for (const key in obj) {
            if (!Object.prototype.hasOwnProperty.call(obj, key))
                continue;
            if (typeof obj[key] !== "number")
                return false;
        }
        return true;
    };
    if (typeof color === "object" && color && "hue" in color && "saturation" in color && "lightness" in color && verifyNumberRecord(color)) {
        try {
            createHSLColor(color);
            return true;
        }
        catch {
            return false;
        }
    }
    else
        return false;
};
export const wrapIndexedColor = (color) => {
    if (verifyHexColor(color)) {
        return color;
    }
    else if (verifyRGBColor(color)) {
        return wrapRGBColor(color);
    }
    else if (verifyHSLColor(color)) {
        return wrapHSLColor(color);
    }
    else {
        return throwOnNever(color);
    }
};
export const validateIndexedColor = (color) => {
    return validateHexColor(color) || validateRGBColor(color) || validateHSLColor(color);
};
