import { createTupleFromCallback } from "../baseTypes";
import { boardDimension } from "./movegen/GameInformation/GameData";
export const initializeBoardSquares = (baseValue) => createTupleFromCallback(() => createTupleFromCallback(baseValue, boardDimension), boardDimension);
export const isVerticalPlacement = (color) => color % 2 === 0;
export function getVerticalPlacementModulus(num) {
    const result = num % 2;
    if (result !== 0 && result !== 1)
        throw new Error(`Invalid number passed: ${num}`);
    return result;
}
export function getHorizontalPlacementModulus(num) {
    const result = num % 2 ^ 1;
    if (result !== 0 && result !== 1)
        throw new Error(`Invalid number passed: ${num}`);
    return result;
}
