import { Board } from "../../../Board/Board";
import { DisplaySettings } from "../../../Board/BoardInterface";
import { VariantRule } from "../../VariantRule";
import { variantRuleColors } from "../../VariantRuleInterface";
import { chessGlyphIndex } from "@client/fonts/chessGlyphIndex";
const tag = "blindfold";
export class Blindfold extends VariantRule {
    static {
        VariantRule.initVariantRule(Blindfold);
    }
    getDecoratorType() {
        return Board;
    }
    getPublicProperties() {
        return {
            parameterValue: true,
            information: {
                name: "Blindfold",
                description: "Pieces are invisible",
                tag,
                color: variantRuleColors.visual,
                displayIcon: chessGlyphIndex.blindfold
            }
        };
    }
    matchesPGNDeclaration(match) {
        return /^Blindfold$/i.test(match);
    }
    serializeToParsingForm() {
        return "Blindfold";
    }
    isDisabled({ variantDataRules }) {
        return variantDataRules.fogOfWar;
    }
    getSquareVisibility() {
        const squareVisibility = this.callHandler("getSquareVisibility", arguments);
        return squareVisibility.map((r, i) => r.map((v, j) => {
            if (this.decorator.board[i][j].isWall()) {
                return v;
            }
            else {
                return [...v, DisplaySettings.Blindfolded];
            }
        }));
    }
}
