export const verifyFunctionType = (func) => typeof func === "function";
export const createTuple = (val, length) => Array.from({ length }).fill(val);
export const createTupleFromCallback = (val, length) => Array.from({ length }, val);
export const verifyTupleType = (arr, length) => arr.length === length;
export function assertNonUndefined(data) {
    if (data == null)
        throw new Error("Expected the data above to be anything but null or undefined");
}
export function throwOnNever(arg) {
    console.dir(arg);
    throw new TypeError(`Unexpected argument that is supposed to be of type never`);
}
export function importAll(r) {
    r.keys().forEach(r);
}
export function getEnumKeys(object) {
    const resultingArray = [];
    let key;
    for (key in object)
        resultingArray.push(key);
    return resultingArray;
}
