import { verifyPieceLetter, pieceControlConfigSettings, defaultPieces } from "@moveGeneration/PieceControl/PieceControlInterface";
import { compileEnumeration, prefixWithIndefiniteArticle } from "@client/ts/utils/StringFormatUtils";
import { PieceControl } from "../../../PieceControl/PieceControl";
import { VariantRule } from "../../VariantRule";
const tag = "promotionPieces";
export class PromoteTo extends VariantRule {
    static {
        VariantRule.initVariantRule(PromoteTo);
    }
    promotionPieces;
    constructor(promotionPieces) {
        super();
        this.promotionPieces =
            Array.isArray(promotionPieces) &&
                promotionPieces.every((p) => typeof p === "string" && verifyPieceLetter(p))
                ? promotionPieces
                : [defaultPieces.queen, defaultPieces.rook, defaultPieces.bishop, defaultPieces.knight];
    }
    getDecoratorType() {
        return PieceControl;
    }
    getPublicProperties() {
        const pieceNames = this.promotionPieces.reduce((p, c) => {
            const pieceName = pieceControlConfigSettings[c].naming.name;
            return [...p, pieceName];
        }, []);
        return {
            parameterValue: this.promotionPieces,
            information: {
                name: "Promotion Pieces",
                textualForm: "",
                description: `Pawns promote to ${prefixWithIndefiniteArticle(compileEnumeration(pieceNames))}`,
                tag
            }
        };
    }
    matchesPGNDeclaration(match) {
        const matchArray = match.match(/^PromoteTo=([A-Zx]+)$/i);
        if (matchArray) {
            const inputPromotion = matchArray[1].split("").filter((p) => verifyPieceLetter(p));
            if (inputPromotion.length)
                this.promotionPieces = inputPromotion;
            return true;
        }
        else {
            return false;
        }
    }
    serializeToParsingForm() {
        return `PromoteTo=${this.promotionPieces.join("")}`;
    }
    isDisabled() {
        return false;
    }
    initDecoratorSettings() {
        if (this.decorator.hooks.usePawnLogic) {
            this.decorator.hooks.usePawnLogic.promotionPieces = this.promotionPieces;
        }
        for (const decorator of this.wrappingDecorators)
            decorator.initDecoratorSettings?.();
    }
}
